import { Block, Node } from '@ali/4ever-cangjie';
import { JsonML } from '@ali/4ever-utils';
import { ELEMENT_TYPE } from '../../utils/meta';
import Model from '../models';

export default {
  match(node: Node): boolean {
    return Model.isColor(node);
  },

  convert(node: Block): JsonML {
    return [ELEMENT_TYPE, node.data];
  },
};
